import React from "react";
import './FormAddOrders.scss'
import InputC from "../../components/Input/Input";
import SelectInputRegion from "../Input/SelectInputRegion";
import UIButton from "../Button";

const FormAddOrders = () => {

    const cargoOrder = [
        '   1',
        '   2',
        '   3',
        '   4'
    ]

    const handleCargoChange = value => {
        console.log(value)
    }

    return(
        <div className='FormAddOrders'>
            <h6></h6>
            <div className='rowAddOrders'>
                <div className='item orderId'>
                    <label></label>
                    <span>1</span>
                </div>
                <div className='item'>
                    <InputC placeholderName='' typeName='text' labelText='' />
                </div>
                <div className='item'>
                    <InputC placeholderName='-' typeName='text' labelText='' />
                </div>
                <div className='item'>
                    <InputC placeholderName='' typeName='text' labelText=' $' />
                </div>
                <div className='item'>
                    <InputC placeholderName=', ' typeName='text' labelText=', ' />
                </div>
            </div>
            <div className='rowAddOrders'>
                <div className='item orderId'>
                    <span>2</span>
                </div>
                <div className='item'>
                    <InputC placeholderName='' typeName='text' />
                </div>
                <div className='item'>
                    <InputC placeholderName='-' typeName='text' />
                </div>
                <div className='item'>
                    <InputC placeholderName='' typeName='text' />
                </div>
                <div className='item'>
                    <InputC placeholderName=', ' typeName='text' />
                </div>
                <div className='item itemDelete'>
                    <UIButton icon='#trashBox' />
                </div>
            </div>
            <div className='rowAddOrders rowCargoOrder'>
                <div className='item orderId'>
                    <span>3</span>
                </div>
                <div className='item'>
                    <SelectInputRegion value={cargoOrder[0]} values={cargoOrder} handleChange={handleCargoChange} />
                </div>
                <div className='item'>
                    <InputC placeholderName='-' typeName='text' />
                </div>
                <div className='item'>
                    <InputC placeholderName='' typeName='text' />
                </div>
                <div className='item'>
                    <InputC placeholderName=', ' typeName='text' />
                </div>
                <div className='item itemDelete'>
                    <UIButton icon='#trashBox' />
                </div>
            </div>
            <div className='rowAddOrders'>
                <div className='item' />
                <div className='item buttonsC'>
                    <UIButton icon='#plusCircle' name=' ' />
                    <UIButton icon='#kargoIcon' name=' ' />
                    <span className='summaryText'></span>
                </div>
                <div className='item sumItemCount'>
                    <InputC placeholderName='-' typeName='text' value='0' />
                </div>
                <div className='item sumItemPrice'>
                    <InputC innerLabel='$' value='0' typeName='text' />
                </div>
                <div className='item sumItemWeight'>
                    <InputC innerLabel='' value='0' typeName='text' />
                </div>
            </div>
        </div>
    )
}

export default FormAddOrders